import './index.less';
import * as checkBox from '../../widgets/checkBox/index.js';
import * as radio from '../../widgets/radio/index.js';
import * as inputTextarea from '../../widgets/InputTextarea/index.js';
import * as popover from '../../widgets/popover/index.js';
import * as fileUploader from '../../widgets/fileUploader/index.js';
import * as nav from '../../widgets/nav/index.js';
import * as btn from '../../widgets/btn/index.js'; 
import * as dialog from '../../widgets/dialog/index.js';
import * as list from '../../widgets/list/index.js';

var Inner = React.createClass({
    render: function(){
    	return (
    		<div>
    			<div className="section">
    				<checkBox.Inner />
    			</div>

    			<div className="section">
    				<radio.Inner />
    			</div>
    			
    			<div className="section">
    				<inputTextarea.Inner />
    			</div>
    			
    			<div className="section">
    				<popover.Inner />
    			</div>

                <div className="section">
                    <fileUploader.Inner />
                </div>

                <div className="section">
                    <nav.Inner />
                </div>

                <div className="section">
                    <nav.NavTop />
                </div>

                <div className="section">
                    <btn.Inner />
                </div>

                <div className="section">
                    <dialog.Inner />
                </div>

                <div className="section">
                    <list.Inner />
                </div>

    		</div>
    	)
    }
});

React.render(
    <Inner />,
    document.getElementById('inner')
);


